    .container {
        position:relative;
        width:1200px;
        margin: 0 auto;
        overflow: hidden;
        overflow-y: auto;
        background: red;
        .text-center {
           width:100%;
           overflow: hidden;
           font-size: 24px;
           text-align: center;
           margin-bottom:100px;
        }
        ul.list-cont{
            width:100%;
            overflow:hidden;
            
            li.list-item{
                width:30%;
                height:200px;
                background: pink;
                float: left;
                margin-right:5%;
                margin-top:30px;
                &:nth-child(3n){
                    margin-right: 0;
                }
            }
        }
    }
