/*头部样式*/
.navbar {
    position:relative;
    top:0;
    left:0;
    width:100%;
    padding:5px 0;
    background-color: rgba(0,0,0,0.2);
    overflow: hidden;
    color:#fff;
    .container{height:100%}
    .nav-brand {
        display:inline-block;
        width:200px;
        overflow: hidden;
        vertical-align: middle;
        img{
            width:100%;
            height:auto;
            vertical-align: middle;
            display: inline-block;
        }
    }
    .nav-list {
        display: inline-block;
        vertical-align: middle;
        li {
            display:inline-block;
            vertical-align: middle;
            padding: 0 16px;
            font-size:18px;
            &:first-child {
               margin-left: 18px;
            }
        }
    }
    .nav-right {
        display: inline-block;
        vertical-align: middle;
    }
    .tab-group {
        font-size:0;
        .tab {
            padding-top: 24px;
            font-size:14px;
            display: inline-block;
            float:right;
            cursor:pointer;
             &:before { 
                content: '';
                width:15px;
                height: 22px;
                display: inline-block;  
                vertical-align: bottom;
             }
             &:first-child {
                 margin-left: 15px;
                &:before {
                   width: 22px;
                   background: url(../imgs/nav-icon.png) no-repeat 0px 0px;
                   margin-right: 5px;
                }
                &+span {
                     margin-left: 15px;
                     &:before {
                         width: 24px;
                         background: url(../imgs/nav-icon.png) no-repeat -70px 0px;
                          margin-right: 7px;
                          position: relative;
                          top: 1px;
                     }
                    &+span {
                       &:before {
                           background:url(../imgs/nav-icon.png) no-repeat -48px 0;
                           margin-right:10px;
                        }
                    }
                }
            }
        }
    }
    .tel {
       padding: 11px 0;
       font-size:24px;  
       padding-left:30px;
        &:before { 
            content: '';
            width:18px;
            height: 24px;
            display: inline-block; 
            background:url(../imgs/nav-icon.png) no-repeat -24px 0;
            vertical-align: text-bottom;
        }
       a {color:#fff;position:relative\0;top:3px\0;} 
    }
}

