.footer{
    min-width:1200px;width:100%;overflow:hidden;background-color: rgba(0,0,0,.3);padding:20px 0;
    color:#fff;
    &.index{
       position:absolute;bottom:0;left:0;
    }
    .footer-list {
        li {
            font-size:16px;
            display: inline-block;
            padding-right:12px;
            &:after {
                content: '|';
                display:inline-block;
                color:#fff;
                padding-left:12px;
            }
            &:last-child {
                &:after {
                    content: '';
                }
            }
        }
    }
    .put-records {font-size:13px;margin-top:12px;}
}


