/*drag*/
.label-move-valid {vertical-align:top;}

#slider {
    width: 244px;
    height: 36px;
    position: relative;
    border-radius: 2px;
    background-color: #dae2d0;
    overflow: hidden;
    text-align: center;
    -ms-user-select: none;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    display: inline-block;
}
#slider_bg {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  background-color: #7AC23C;
  z-index: 1;
}

#label {
  width: 36px;
  position: absolute;
  left: 0;
  top: 0;
  height: 36px;
  display: inline-block;
  border: 1px solid #cccccc;
  background: #fff;
  z-index: 3;
  cursor: move;
  color: #ff9e77;
  font-size: 16px;
  font-weight: 900;
  background:url(../imgs/move-slider.png) no-repeat;
  background-size: 100%; 
}

#labelTip {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  font-size: 13px;
  font-family: 'Microsoft Yahei', serif;
  color: #787878;
  line-height: 36px;
  text-align: center;
  z-index: 2;
}

@media screen and (min-width: 1601px) { 
   #slider { 
        width: 423px;
        height: 52px;
    }
    #label {
        width: 52px;
        height:52px;
    }
    #labelTip {
        line-height: 52px;
    }
}